@import url(https://fonts.googleapis.com/css?family=Shadows+Into+Light);
.UserTaskForm * {
	box-sizing: border-box;
	font-size: 16px;
}

.UserTaskForm table, .UserTaskForm th, .UserTaskForm td {
    border-collapse: collapse;
    border: 1px solid black;
}

.UserTaskForm table {
    width: 100%;
    border: 2px solid black;
    margin-bottom: 10px;
}

.UserTaskForm {
    width: 100%;
    max-width: 768px ;
}

.UserTaskForm td:first-child  { 
    width: 33.33%;
    background-color:  #F1F1F1;
    text-align: right;
    padding: 20px 20px 20px 20px;
}
.UserTaskForm td:last-child { 
    width: 66.67%;
}

.UserTaskForm textarea {
    border: 1;
    width: 100%;
    resize: vertical; 
    height: 200px;
}

.UserTaskForm td[colSpan="2"] {
    text-align: start;
} 

.UserTaskForm input[type="text"] {
    width: 100%;
    height: 100%;
}	

.UserTaskForm h3 {
    margin-top: 8px;
    margin-bottom: 0px;
    font-size: 22px;
}

.UserTaskForm h2 {
    font-size: 24px;
}

.UserTaskForm a.downloadLink {
    display: block;
    text-decoration: none;
    padding: 5px;
    text-transform: uppercase;
    font-size: 20px;
    background-color: rgb(255, 70, 70);
    box-shadow: inset 2px 2px 1px rgb(255, 199, 199);
    color: white;
    font-weight: 500;
}

span.formName {
    float: right;
}

/* READONLY FIELDS */
input[readonly], textarea[readonly], select[disabled] {
    background-color: white;
	border: 0;
	font-size: 22px;
	padding: 0px 10px 0px 10px;
	font-family: 'Shadows Into Light', cursive;
}
textarea[readonly] {
	background-color: #EEE;
}

select[disabled]::-ms-expand {
    display: none;
}

select[disabled] {
    -webkit-appearance: none;
    -moz-appearance: none;
    text-indent: 1px;
    text-overflow: '';
}
.FormContainer button {
    font-size: inherit;
    font-family: inherit;
    background-color: rgb(255, 70, 70);
    box-shadow: inset 1px 1px 1px rgb(255, 132, 132);
    color: white;
    font-weight: 500;
    margin: 12px 0px;
}

#formHolder { 
    height: 60%;
}

.FormContainer {
    box-sizing:border-box;
}

.FormContainer .uslovDiv label {
    font-size: 20px;
}
.TaskItem {
   background-color: #DDD;
   border-bottom: 1px dotted black;
   padding: 10px;
   text-align: left;
}

.TaskItem .taskTitle {
    color: red;
    font-weight: 500;
}

.TaskItem > * {
    margin: auto;
}


.TaskItem .date {
    font-size: 20px;
}

.TaskItem:hover {
    background-color: rgb(244, 244, 244);
    font-style: italic;    
}


.TaskItem.selected {
    background-color: white;
    font-style: italic;  
    border-left: 1px solid red;  
}
.Camunda {
    margin: 20px auto;
    border: 6px solid white;
    padding: 8px;
    
    position: absolute;
    left: 40px;
    right: 40px;
    top: 40px;
    bottom: 0;
    max-width: 1200px;

    box-shadow: 
        inset 1px 1px 4px black,
        inset -1px -1px 4px black;

    -webkit-animation: fadein 0.8s;

            animation: fadein 0.8s;
}

@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

.Camunda button[name="claimTask"], .Camunda button[name="unclaimTask"] {
    font-size: inherit;
    font-family: inherit;
    background-color: rgb(255, 70, 70);
    box-shadow: inset 1px 1px 1px rgb(255, 132, 132);
    color: white;
    font-weight: 500;
    margin: 0px;
}





.TaskList {
    float: left;
    padding: 5px 0px 0px 5px;
    background-color: #e9e9e9;
    border-right: 2px solid rgb(225, 46, 46);

    min-width: 20%;
    width: 33%;
    max-width: 66%;
    
    height: 100%;
    
    overflow-y: scroll;
    overflow-x: hidden;
    
    resize: horizontal;
}

.TaskList .flex-container {
    display: flex;
    flex-direction: column;
}

.TaskList .start-process-btn {
    font-size: 18px;
    margin: 1px 1px 10px 1px;
    padding: 8px;
    font-weight: bold;

    background-color: rgb(225, 46, 46);
    color: #FFF;
    width: 100%;
}


.WorkSpace {
    background-color: white;
    height: 100%;
    overflow-y: scroll;
    text-align: left;
    padding: 10px;
}

.WorkSpace * {
    margin: 0px;
}

.WorkSpace .title {
    color: red;
    font-weight: 500;
}

.Login {
    width: 350px;
    margin: 140px auto;
    text-align: center;
    padding: 50px;  
    background-color: white; 
    box-shadow: 
        3px 4px 3px rgb(77, 1, 1),
  inset 3px 4px 1px rgb(209, 25, 25);
    
    -webkit-animation-name: fadein;
    
            animation-name: fadein;
    -webkit-animation-duration: 0.8s;
            animation-duration: 0.8s;
}

.Login form { 
    text-align: center;
    margin: auto;
}

.Login form * {
    display: block;
    width: 100%;
}

.Login form input[type='text'], .Login form input[type='password'] { 
    font-size: inherit; 
    font-family: inherit; 
    background-color: #e9e9e9;
    border: none;
    margin-bottom: 4px;
}

.Login form button {
    font-size: inherit;
    font-family: inherit;
    background-color: rgb(255, 70, 70);
    box-shadow: inset 1px 1px 1px rgb(255, 132, 132);
    color: white;
    font-weight: 500;
    margin-top: 12px;
}

@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

.Nav {
    width: 100%;
    height: 45px;
    line-height: 45px;
    background-color: #EEE;
    border-top: 1px solid rgb(75, 0, 0);
    border-left: 2px solid rgb(75, 0, 0);
    box-shadow: 0px 1px 2px #333;
}
.Nav img {
    height: 35px;
    width: 35px;
    margin-top: 5px;
}


.Nav ul {
    list-style-type: none;
    margin: 0; 
    padding: 0;
    height: 100%;
}
  
.Nav li {
    height: 100%;
}
.Nav li a {
    display: block;
    height: 100%;
    text-decoration: none;
    color: inherit;
    padding: 0 7px;
    line-height: 45px;
}
.Nav li a:hover {
    background-color: #999;
}


.Nav .dropdown {
    position: relative;
}
.Nav .dropdown-content {
    right: 0;
    display: none;
    position: absolute;
    background-color: #EEE;
    min-width: 100px;
    box-shadow: 0px 2px 2px #333;
    z-index: 1;
}
.Nav .dropdown:hover .dropdown-content {
    display: block;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
  margin: 0;
  padding: 0;
  
  font-family: "Open Sans Condensed", sans-serif;
  font-size: 24px;
  background-color: rgb(225, 46, 46);
}

* {
  box-sizing: border-box;
}

